.page-link {
    @include a();
    @include font(1.6rem, 130%, 500);
    position: relative;
    text-transform: capitalize;

    &::after {
        @include pseudo();
        bottom: -5px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: $accent;
    }

    &:hover {
        color: $accent;
        transition: 0.2 ease-in;

        &::after {
            background-color: $dark;
        }
    }

    &:active {
        color: $grey;
        transition: 0.2 ease-in;
    }
}
