.jewel {
    margin-top: 63px;
    margin-bottom: 120px;
}

.jewel__container {
    display: grid;
    grid-template-columns: minmax(400px, 600px) minmax(300px, 370px);
    grid-column-gap: 100px;
}

.jewel__description {
    margin-top: -3px;
    display: flex;
    flex-direction: column;
}

.jewel__description h2 {
    text-align: left;
    line-height: 100%;
    margin-bottom: 18px;
    letter-spacing: 0;
}

.jewel__description p {
    @include font(2.6rem, 100%, 400);
    @include fields();
    text-align: left;
    margin-bottom: 43px;
}

.jewel__table {
    border-collapse: collapse;
    width: 100%;
}

.jewel__info {
    position: relative;
}

.jewel__info label {
    color: $grey;
    text-align: left;
    padding: 20px 0;
    margin-right: 40px;
    margin-left: -8px;
    cursor: pointer;
}

.jewel__info input {
    visibility: hidden;
    margin: 0;
    padding: 0;
    width: 0;
    height: 0;

    &:first-of-type:checked ~ .jewel__label-1,
    &:last-of-type:checked ~ .jewel__label-2 {
        color: $dark;
    }

    &:first-of-type:checked ~ .jewel__info-block-1 {
        display: block;
    }

    &:last-of-type:checked ~ .jewel__info-block-2 {
        display: table;
    }
}

.jewel__info p {
    @include fields();
    @include font(1.6rem, 160%, 400);
    margin-top: 30px;
    min-height: 147px;
}

.jewel__info-block {
    display: none;
    margin-top: 20px;
}

.jewel__tr {
    border-bottom: 1px solid $pale;
}

.jewel__td {
    @include font(1.4rem, 130%, 500);
    padding: 11px 0;

    &--right {
        font-weight: 400;
        text-align: right;
    }
}

.jewel__add-link {
    @include font(1.6rem, 130%, 500);
    padding: 24px;
    margin-top: auto;
    border: none;
    font-family: $poppins;
    display: block;
    cursor: pointer;
    text-align: center;
    background-color: $accent;
    color: $white;
    text-decoration: none;

    &:hover {
        background-color: $brown;
        color: white;
        transition: 0.2 ease-in;
    }

    &:active {
        background-color: rgba($accent, 0.7);
        transition: 0.2 ease-in;
    }
}

.jewel__features-list {
    @include ul();
    @include font(1.4rem, 130%, 500);
    margin-top: 20px;
    display: flex;
    font-weight: 500;
    justify-content: space-between;
    text-transform: capitalize;
    color: $grey;
    gap: 20px;
}

@media (max-width: $desktop) {
    .jewel {
        margin-top: 30px;
        margin-bottom: 70px;
    }

    .jewel__container {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 40px;
    }

    .jewel__features-list {
        @include font(1.2rem, 130%, 500);
        margin-top: 18px;
    }
}

@media (max-width: $tablet) {
    .jewel {
        margin-top: 0;
        margin-bottom: 58px;
    }

    .jewel__container {
        grid-template-columns: 1fr;
        grid-row-gap: 48px;
    }

    .jewel__description {
        justify-content: flex-start;
    }

    .jewel__description h2 {
        margin-bottom: 21px;
    }

    .jewel__description p {
        margin-bottom: 32px;
    }

    .jewel__features-list {
        text-align: center;
        margin-top: 12px;
    }

    .jewel__info {
        position: relative;
    }

    .jewel__info label {
        margin-right: 42px;
    }

    .jewel__add-link {
        margin-top: 38px;
    }
}
