.new {
    margin-bottom: 176px;
}

.new__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.new__container h2 {
    letter-spacing: normal;
    margin-bottom: 22px;
}

.new__page-link {
    margin-bottom: 64px;
}

.new__slider {
    margin-bottom: 22px;
}

.new__inner {
    @include ul();
    display: flex;
    flex-shrink: 1;
}

.new__bullets {
    @include ul();
    display: flex;
    justify-content: center;
    gap: 8px;
}

@media (max-width: $desktop) {
    .new {
        margin-bottom: 124px;
    }

    .new__page-link {
        margin-bottom: 56px;
    }

    .new__slider {
        margin-bottom: 10px;
    }
}

@media (max-width: $tablet) {
    .new {
        margin-bottom: 116px;
    }

    .new__container h2 {
        margin-bottom: 18px;
    }

    .new__page-link {
        margin-bottom: 45px;
    }

    .new__slider {
        margin-bottom: 4px;
    }
}
