.nav__list {
    @include ul();
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-left: 10px;

    &--mobile {
        display: none;
    }
}

.nav__link {
    @include a();
    padding: 5px 8px;
    display: block;

    &:hover {
        color: $accent;
        transition: 0.2 ease-in;
    }

    &:active {
        color: $grey;
        transition: 0.2 ease-in;
    }

    &--mobile {
        @include font(1.8rem, 23.4px, 500);
        color: rgba($white, 0.75);
    }
}

@media (max-width: $desktop) {
    .nav__list {
        flex-direction: column;
        align-items: flex-start;
        margin-left: 0;
        gap: 11px;
        margin-bottom: 30px;

        &--mobile {
            @include font(1.8rem, 23.4px, 500);
            @include ul();
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 30px;
        }
    }

    .nav__link {
        @include font(2.4rem, 36px, 500);

        &:hover {
            color: $white;
            text-decoration: underline;
            transition: 0.2 ease-in;
        }

        &:active {
            color: $grey;
            transition: 0.2 ease-in;
        }

        &--mobile {
            @include font(1.8rem, 23.4px, 500);
        }
    }
}

@media (max-width: $tablet) {
    .nav__list {
        margin-bottom: 20px;

        &--mobile {
            margin-bottom: 20px;
        }
    }
}
